.popular {
   margin-bottom: 80px;
   @media (max-width: 1024px) {
      margin-bottom: 60px;
   }
   &__wrapper {
      display: flex;
      flex-direction: column;
      align-items: center;
   }

   &__descr {
      font-size: 18px;
      line-height: 30px;
      color: var(--color-gray);
      text-align: center;
      max-width: 426px;
      margin-bottom: var(--offset-medium);
      @media (max-width: 1024px) {
         margin-bottom: 40px;
      }
   }

   &__items {      
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      @media (max-width: 700px) {
         justify-content: center;
      }      
   }

   &__card {
      flex: 0 0 32%;          
      opacity: 0;
      visibility: hidden;
      height: 0;
     margin-bottom: 0;
      @media (max-width: 1024px) {
     
      }  
      @media (max-width: 991px) {
         flex: 0 0 48%;
        
      }
      @media (max-width: 700px) {
         flex: 0 0 60%;
      }
      @media (max-width: 600px) {
         flex: 0 0 100%;
        
      }
      &:nth-child(1), &:nth-child(2), &:nth-child(3) {
         display: inline-block;
         transform: translateX(-50px);
         opacity: 0;
         visibility: hidden;
         height: auto;
         padding: 34px;
         margin-bottom: 100px;   
         @media (max-width: 991px) {
            margin-bottom: 20px;
           }
      }     
      &_visible {
        height: auto;
        opacity: 1;
        visibility: visible;
        padding: 34px;
        margin-bottom: 100px;  
        @media (max-width: 991px) {
         margin-bottom: 20px;
        }
      }  
   }
}
