.hero {
   position: relative;
   overflow: hidden;
   @media (max-width: 1024px) {
      height: calc(100vh - 106px);
   }
   &__bg {
      position: absolute;
      right: 0;
      top: 0;
      z-index: -1;
      @media (max-width: 1024px) {
         height: calc(100vh - 106px);
      }
   }

   &__wrapper {
      max-width: 600px;
   }
   &__title {
      @media (max-width: 800px) {
         max-width: 400px;
      }      
   }
   &__descr {
      color: var(--color-gray);
      font-size: 18px;
      line-height: 30px;
      margin-bottom: var(--offset-secondary);
      max-width: 480px;
      @media (max-width: 800px) {
         max-width: 400px;
      }
   }

   &__input-box {
      max-width: 500px;
      width: 100%;
      margin-bottom: 110px;
      @media (max-width: 800px) {
         max-width: 400px;
      }
   }

   &__input {
      @media (max-width: 800px) {
         padding-left: 40px;
      }
   }

   &__btn {
      position: absolute;
      right: 10px;
      top: 50%;
      transform: translateY(-50%);
      padding: 12px 36px;
      padding: 12px 20px;
      background-color:var(--color-accent);
      border-radius: 10px;
      color:var(--color-white);
      font-weight: 700;
      letter-spacing: 1px;
      box-shadow: 0px 0px 10px 1px transparent;
      transition: .3s;  
      &:hover {
         box-shadow: 0px 0px 10px 1px var(--color-accent);
      }  
   }

   &__list {
      display: flex;
   }

   &__item {
      &:not(:last-child) {
         margin-right: 24px;
      }     
      &:nth-child(1) {
         & svg {
            animation: light 4s infinite;
            animation-delay: 3s;
         }        
      }
      &:nth-child(2) {
         & svg {
            animation: light 4s infinite;
            animation-delay: 4s;
         }        
      }
      &:nth-child(3) {
         & svg {
            animation: light 4s infinite;
            animation-delay: 5s;
         }       
      }  
   }

   &__icon {
      fill: var(--color-gray);
      transition: fill 0.3s ease-in-out;        
   }
}


@keyframes light {
   0% {
      fill: var(--color-gray);
   }

   25% {
      fill: var(--color-gray);
   }

   50% {
      fill: var(--color-accent);
   }

   75% {
      fill: var(--color-accent);
   }

   100% {
      fill: var(--color-gray);
   }
}